export const loadWAFData = async () => {
  return await new Promise((resolve, reject) => {
    window.require(
      ["DS/WAFData/WAFData"],
      (module) => {
        resolve(module);
      },
      reject
    );
  });
};

export const fetchCsrfToken = async () => {
  try {
    // let securityContext1 = "VPLMAdmin.Company Name.Default";
    const WAFData = await loadWAFData();
    // console.log("Security Context is:", securityContext1);

    const collabSpaceUrl =
      "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/modeler/pno/person/?current=true&select=collabspaces";
    const collabSpaceResponse = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(collabSpaceUrl, {
        method: "GET",
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });

    console.log("Collab space Response is:", collabSpaceResponse);
    const roleName = collabSpaceResponse.collabspaces[0].couples[0].role.name;
    const orgName =
      collabSpaceResponse.collabspaces[0].couples[0].organization.name;
    const collabName = collabSpaceResponse.collabspaces[0].name;
    const securityContext1 = `${roleName}.${orgName}.${collabName}`;
    const csrfURL =
      "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/application/CSRF?tenant=OI000186152";

    const response = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(csrfURL, {
        method: "GET",
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });

    const csrfToken = response.csrf.name;
    const csrfValue = response.csrf.value;
    const securityContextHeader = "SecurityContext";
    const securityContextValue = securityContext1;

    const headers = {
      [csrfToken]: csrfValue,
      [securityContextHeader]: securityContextValue,
    };
    console.log("Headers are:", headers);
    return headers;
  } catch (error) {
    console.error("[CSRF] Failed to fetch token:", error);
    return; // Return empth so that the else block should execute.
  }
};

